import React, { useState, useRef } from 'react';
import { isSSR } from '@front-toolkit/utils';
import PortalTooltip from 'core/src/Tooltip/PortalTooltip';

import { TooltipProps } from './types';

import styles from './styles.scss';

const Tooltip: React.FC<TooltipProps> = ({
  text,
  placement,
  width,
  hint,
  dataTestId,
  size = 'default',
  zIndex,
  children,
  tailAlign
}) => {
  const [isHover, setIsHover] = useState(false);
  const wrapper = useRef<HTMLDivElement>(null);
  const isVisible = isHover && !isSSR;

  const handleMouseOver = (): void => {
    setIsHover(true);
  };
  const handleMouseOut = (): void => {
    setIsHover(false);
  };

  return (
    <div
      className={ styles.wrapper }
      data-test-id={ dataTestId }
      onMouseOver={ handleMouseOver }
      onFocus={ handleMouseOver }
      onMouseOut={ handleMouseOut }
      onBlur={ handleMouseOut }
    >
      { /** Оборачиваемый в тултип элемент */ }
      <div className={ styles.children } ref={ wrapper }>
        { children }
      </div>
      { /** Тултип */ }
      { isVisible && (
        <PortalTooltip
          text={ text }
          placement={ placement }
          width={ width }
          hint={ hint }
          size={ size }
          zIndex={ zIndex }
          target={ wrapper?.current }
          tailAlign={ tailAlign }
        />
      ) }
    </div>
  );
};

export { Tooltip };
